.page-regist {
  .btnArea {
    text-align: center;
    .c-button {
      width: 60%;
      height: 30px;
      line-height: 28px;
    }
  }
  &__kakuninTtl {
    background-color: $color-primary;
    color: #FFF;
    width: 100%;
    padding: 5px 0 5px 0;
    margin: 5px  0 0;
    text-align: center;
    }
  #new-member {
    #entry-comment {
      margin: 10px 0 10px;
      font-size: 80%;
    }
    .entry {
      &__ttl {
        @include font-line(16, 24);
        background-color: #fffaf3;
        padding: 5px;
        font-weight: normal;
        margin: 10px 0;
        clear: both;
        p {
          margin: 0;
          padding: 0;
        }
        .hissu,
        .nini {
          display: inline-block;
          font-size: 70%;
          font-weight: normal;
          color: #FFF;
          padding: 2px 5px 2px 5px;
          margin-right: 5px;
          position: relative;
          top: -2px;
        }
        .hissu {
          background-color: #F00;
        }
        .nini {
          background: #CCC;
        }
        .small-text {
          font-size: 60%;
        }
      }
      input[type="text"],
      input[type="tel"],
      input[type="email"],
      select,
      textarea {
        @include font-line(14, 28);
      }
      select {
        width: unset;
      }
      textarea {
        width: 100%;
      }
    }
    #mail-comment {
      width: 97%;
    }
    .entry .small-text {
      font-size: 70%;
    }
    .new-mConfirm {
      &__ttl {
        @include font-line(20, 24);
        color: #000;
        border-bottom: 2px solid #000;
        text-align: left;
        padding: 12px 10px 8px 10px;
        margin-bottom: 30px;
      }
      h3 {
        background-color: transparent;
        border-bottom: 3px solid $color-primary;
        color: #333;
        margin-left: -10px;
        margin-right: -10px;
        text-align: center;
      }
      dl {
        line-height: 120%;
        dt {
          background-color: #fffaf3;
          padding-top: 5px;
          padding-bottom: 5px;
          padding-left: 10px;
          margin-bottom: 10px;
          &:first-child {
            margin-top: -10px;
          }
        }
        dd {
          padding-top: 2px;
          padding-bottom: 5px;
          min-height: 15px;
          margin-left: 0;
          margin-bottom: 10px;
          line-height: 140%;
        }
      }
      .buttonBlock {
        .button {
          height: 40px;
          border-radius: 20px;
          width: 100%;
          margin: 0 auto;
          @include font-line(14, 14);
        }
      }
    }
  }

  .enter-TextBtn a img {
    margin: 10px auto;
  }

  #new-member {
    .new-mConfirm .small-text {
      font-size: 70%;
    }
    .enter-TextBtn {
      font-size: 80%;
      text-align: center;
      margin: 20px 0;
      padding: 0 10px;
      .estateButton {
        width: 80%;
        height: 50px;
        @include font-line(15, 15);
      }
    }
    .enter-TextBtn2 {
      font-size: 80%;
      text-align: left;
      margin-top: 20px;
      margin-bottom: 5px;
      padding: 0 10px;
    }
    .entry {
      #doui {
        margin-top: 10px;
        text-align: center;
        background-color: #F7F7F7;
        padding: 10px;
        margin-right: 10px;
        margin-bottom: 10px;
      }
      .douiBlock{
        &__text{
          margin-top: 0;
        }
      }
      .kojinjyoho {
        font-size: 12px;
        overflow: auto;
        height: 150px;
        padding-top: 10px;
        margin-top: 10px;
        border-top: $color-border medium solid;
        width: 100%;
        margin-right: 10px;
        margin-bottom: 10px;
        margin-left: 0;
      }
    }
    #next-step {
      text-align: center;
    }
    .buttonBlock{
      margin: 10px auto;
      &__btn{
        @include font-line(18, 18);
        width: 100%;
        height: 50px;
      }
    }
  }
}
